<script>
  import { onDestroy } from 'svelte';
  import { readable } from 'svelte/store';
  import { tweened } from 'svelte/motion';
  import { cubicOut } from 'svelte/easing';
  import { scaleLinear } from 'd3-scale';
  import { onMount } from 'svelte';

  import { time } from './store.js';
  import { create_interpolator, interpolate } from './cspl.js';
  import { linspace, normalize } from './util.js';
  import { project_onto_pib_states } from './qpib.js';

  let svg;
  let windowHeight = 300;
  let windowWidth = 500;
  $: width = windowWidth;
  $: height = 300;

  const resizeSVG = () => {
    let svgBoundingBox = svg.getBBox();

    width = svgBoundingBox.x + svgBoundingBox.width + svgBoundingBox.x;
    height = svgBoundingBox.y + svgBoundingBox.height + svgBoundingBox.y;
  };

  onMount(resizeSVG);

  $: animate = false;
  $: last_time_value = 0;

  let w = 0.002;
  let c = 0.1;
  let x0 = 2.0;
  let nx = 100;
  let mass = 2000.0;
  let length = 1.0;

  let adjusting = false;
  let adjusted = false;
  let mode = "edit"
  let dragging;

  function create_interpolant(control_points) {
    let xs = control_points.map(cp => { return cp.cx; })
    let ys = control_points.map(cp => { return cp.cy; })

    return create_interpolator(xs, ys);
  }

  function handleClick(event) {
    if (adjusting || animate || (mode=="run")) {
     return
    }

    const cp = {
      cx: xScale.invert(event.offsetX),
      cy: yScale.invert(event.offsetY),
      r: 10
    }

    control_points = control_points.concat(cp).sort(function (a, b) { return a.cx - b.cx });
    interpolant = create_interpolant(control_points);
  }

  function move_dot(event) {
    if (dragging == null) { return; }

    dragging.cx = xScale.invert(event.offsetX);
    dragging.cy = yScale.invert(event.offsetY);

    control_points = control_points.sort(function (a, b) { return a.cx - b.cx; });
    interpolant = create_interpolant(control_points);
  }

  $: get_psi2_points_at_time = (t) => {
    let out = [];
    let x;
    let omega = Math.PI / 1.0;
    let nphi = cn.length;
    for (x = 0; x < nx; x++) {
      let xx = x/(nx-1);
      let val = 0.0;
      let i, j, wij;
      for (i = 0; i < nphi; i++) {
        val += cn[i] * cn[i] * Math.sin((i+1)*omega*xx)**2;
        for (j = 0; j < i; j++) {
          wij = Math.PI**2/(2.0 * mass * length**2) * (j*j - i*i);
          val += cn[i] * cn[j] * 2 * Math.sin((i+1)*omega*xx) * Math.sin((j+1)*omega*xx) * Math.cos(wij * t);
        }
      }
      val *= 2; // for normalization of eigenstates
      out.push({x: xx, y:val});
    }
    return out;
  }

  $: get_psi_points = () => {
    let out = [];
    let x;
    let omega = Math.PI / 1.0;
    let nphi = cn.length;
    for (x = 0; x < nx; x++) {
      let xx = x/(nx-1);
      let val = 0.0;
      let i, j, wij;
      for (i = 0; i < nphi; i++) {
        val += cn[i] * Math.sin((i+1)*omega*xx);
      }
      val *= Math.sqrt(2); // for normalization of eigenstates
      out.push({x: xx, y:val});
    }
    return out;
  }

  $: get_points_at_time = (t) => {
    if (mode == "edit") {
      return get_psi_points();
    }
    else {
      return get_psi2_points_at_time(t);
    }
  }

  $: points = get_points_at_time(animate ? $time.valueOf() - last_time_value : last_time_value);

  $: get_smoothed_curve = (control_points) => {
    let n = 128;
    let xx = linspace(0.0, 1.0, n);

    let yy = interpolate(interpolant, xx);

    let i;
    let out = []
    for (i = 0; i < xx.length; ++i) {
      out.push( { x: xx[i], y: yy[i] } );
    }
    return out;
  };


  $: yTicks = (mode=="edit") ? [-2, -1, 0, 1, 2] : [0, 1, 2];
  const xTicks = [0, 0.25, 0.5, 0.75, 1.0];
  const padding = { top: 20, right: 15, bottom: 20, left: 25 };

  $: xScale = scaleLinear()
    .domain([minX, maxX])
    .range([padding.left, width - padding.right]);

  $: yScale = scaleLinear()
    .domain([Math.min.apply(null, yTicks), Math.max.apply(null, yTicks)])
    .range([height - padding.bottom, padding.top]);

  let control_points = [ { cx: 0, cy: 0 }, { cx: 1, cy: 0 } ]
  let minX = 0.0;
  let maxX = 1.0;

  $: interpolant = create_interpolant(control_points);
  $: smoothed_points = get_smoothed_curve(control_points);
  $: smoothed_path =`M${smoothed_points.map(p => `${xScale(p.x)},${yScale(p.y)}`).join('L')}`;
  $: cn = project_onto_pib_states(smoothed_points, 1e-5);
  $: cn_normal = normalize(cn)
  $: path = `M${points.map(p => `${xScale(p.x)},${yScale(p.y)}`).join('L')}`;
  $: area = `${path}L${xScale(maxX)},${yScale(0)}L${xScale(minX)},${yScale(0)}Z`;
  $: control_path = `M${control_points.map(cp => `${xScale(cp.cx)},${yScale(cp.cy)} ${xScale(cp.cx)},${yScale(cp.cy)}`).join('Q')}`;

  function formatMobile (tick) {
    return "'" + tick.toString().slice(-2);
  }
</script>

<h2>Quantum Particle in a Box</h2>

<div class="chart" on:click={handleClick} bind:clientWidth={windowWidth} bind:clientHeight={windowHeight}>
  <svg bind:this={svg} viewBox="0 0 {width} {height}" width={width} height={height}>
    <!-- y axis -->
    <g class="axis y-axis" transform="translate(0, {padding.top})">
      {#each yTicks as tick}
        <g class="tick tick-{tick}" transform="translate(0, {yScale(tick) - padding.bottom})">
          <line x2="100%"></line>
          <text y="-4">{tick} {tick === 8 ? ' |psi(x)|^2' : ''}</text>
        </g>
      {/each}
    </g>

    <!-- x axis -->
    <g class="axis x-axis">
      {#each xTicks as tick}
        <g class="tick tick-{ tick }" transform="translate({xScale(tick)},{height})">
          <line y1="-{height}" y2="-{padding.bottom}" x1="0" x2="0"></line>
          <text y="-2">{width > 380 ? tick : formatMobile(tick)}</text>
        </g>
      {/each}
    </g>

    <!-- data -->
    <path class="path-area-{mode}" d={area}></path>
    <path class="path-line-{mode}" d={path}></path>

    {#if mode=="edit"}
    <path class="path-line-{mode}" d={smoothed_path}></path>

    <!-- control points -->
    {#each control_points as cp, i}
    <circle cx={xScale(cp.cx)} cy={yScale(cp.cy)} r={cp.r} fill="black"
      on:mouseover="{() => { adjusting = true; }}" on:mouseout="{() => {adjusting = false;}}"
      on:mousedown="{() => {dragging = cp;}}" on:mousemove={move_dot} on:mouseup="{() => {dragging=null;}}"
      on:dblclick="{() => { control_points.splice(i, 1); control_points = control_points.filter( x => { return true; }); } }" />
    {/each}
    {/if}
  </svg>
</div>

<p>Particle in a Box motion</p>
<p>
<button on:click={() => {
  if (mode == "edit") {
    mode = "run"
  }
  else {
    mode = "edit"
    animate = false
    last_time_value = 0
  }
  }}>{ (mode=="edit" ? 'Run' : 'Edit') }
</button>
{#if mode=="run"}
<button on:click={() => {
    animate = !animate;
    let new_time = new Date();
    last_time_value = new_time.valueOf() - last_time_value;
  }}>
  {(animate) ? 'Pause' : 'Play'}
</button>
<button on:click={() => { last_time_value = 0; }}> Reset </button>
{/if}
{#if mode=="edit"}
<button on:click={() => { control_points = [ { cx: 0, cy: 0 }, { cx: 1, cy: 0 } ];}}>
 Clear </button>
{/if}
</p>

<p> Normalized Coefficients: </p>
<p>
{#each cn_normal as c, i}
<i>c</i><sub>{i+1}</sub> = {new Intl.NumberFormat('en-IN', { maximumSignificantDigits: 3}).format(c)} <br>
{/each}
</p>

<style>
  .chart, h2, p {
    width: 100%;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
  }

  svg {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: visible;
  }

  .tick {
    font-size: .725em;
    font-weight: 200;
  }

  .tick line {
    stroke: #aaa;
    stroke-dasharray: 2;
  }

  .tick text {
    fill: #666;
    text-anchor: start;
  }

  .tick.tick-0 line {
    stroke-dasharray: 0;
  }

  .x-axis .tick text {
    text-anchor: middle;
  }

  .path-line-edit {
    fill: none;
    stroke: rgb(100,0,100);
    stroke-linejoin: round;
    stroke-linecap: round;
    stroke-width: 2;
  }

  .path-area-edit {
    fill: rgba(100,0,100,0.2);
  }

  .path-line-run {
    fill: none;
    stroke: rgb(0,100,100);
    stroke-linejoin: round;
    stroke-linecap: round;
    stroke-width: 2;
  }

  .path-area-run {
    fill: rgba(0,100,100,0.2);
  }

</style>



